@import url(https://fonts.googleapis.com/css2?family=Cantarell&family=Red+Rose:wght@300;400&family=Staatliches&family=Varela&display=swap);
:root {
  --color-theme: #59B2A0;
  --color-theme-shadow: #59b2a0b8;
  --color-theme-dark: #3c786c;
  --color-bg: #202124;
  --color-spark-gray: #555555;
  --color-spark-gray-dark: #3C464D;
  --color-google-gray: #bdc1c6;
  --color-silver-main: #F2F2F2;
  --color-silver-main-dark: #b1b1b1;
  --color-wiki-green-main: #83E6CB;
  --color-wiki-green-light: #E1F4EF;
  --center-abs-margin: calc((100vw - 1065px)/2);

  --font-family-main: 'Red Rose', cursive;
  --font-family-google: 'Cantarell', sans-serif;
  --font-family-product: 'Varela', sans-serif;
  --font-family-alt: 'Staatliches', sans-serif;

  --font-p-google: 18px;
  --font-h3-google: 28px;
  --font-h4-google: 24px;

  --font-p-product: 18px;
  --font-h1-product: 34px;
  --font-button-product: 15px;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}


::-webkit-scrollbar {
  width: .7em;
  background: #202124;
  background: var(--color-bg);
}

::-webkit-scrollbar-track {
  margin-block: 0;
  border-radius: 100vw;
}

::-webkit-scrollbar-thumb {
  background: #F2F2F2;
  background: var(--color-silver-main);
  border-radius: 100vw;
  height: 6em;
}

::-webkit-scrollbar-thumb:hover {
  background: #b1b1b1;
  background: var(--color-silver-main-dark);
}

/* for firefox */
@supports (scrollbar-color: var(--color-silver-main) var(--color-silver-main-dark)) {
  * {
    scrollbar-color: #F2F2F2 rgba(0, 0, 0, 0);
    scrollbar-color: var(--color-silver-main) rgba(0, 0, 0, 0);
  }
}


body {
  background: white;
  margin: 0;
  padding: 0;
}

.app-div {
  background-color: #202124;
  background-color: var(--color-bg);
}

.home-page {
  background: #202124;
  background: var(--color-bg);
}

.container {
  --main-width: 1100px;
  width: var(--main-width);
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.page {
  max-height: auto;
  min-height: 100vh;
}

@media screen and (min-width: 980px) {
  .container {
      width: 970px;
  }
}

@media screen and (min-width: 768px) {
  .container {
      width: 750px;
  }
}

@media (min-width: 1200px) {
  .container {
      width: var(--main-width);
  }
}


.navbar-wrap {
    background-color: var(--color-bg);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    width: 100%;
}

.navbar-wrap-no {
    background-color: transparent;
    position: fixed;
    top: 85vh;
    z-index: 999;
    width: 100%;
}

.navbar-wrap.scrolled {
    background-color: var(--color-bg);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.navbar {
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: space-between;
    /* font-family: 'Staalitches', sans-serif; */
    font-family: 'Red Rose', cursive;
    font-size: 18px;
    letter-spacing: 2px;
    color: var(--color-silver-main);
}

.navbar h3 {
    transition: all .2s ease-in;
}

.navbar h3:hover {
    color: var(--color-theme);
    text-decoration: none;
}

.navbar a {
    color: var(--color-silver-main);
    text-decoration: none;
    transition: all .2s ease-in;
}

.navbar a:visited {
    color: var(--color-silver-main);
    text-decoration: none;
}

.navbar a:hover {
    color: var(--color-theme);
    text-decoration: none;
}
.project-page-outer {
    background: white;
}

.project-page {
    background: white;
    font-family: var(--font-family-product);
    color: var(--color-spark-gray);
    font-size: var(--font-p-product);
    padding: 10px 0px;
    position: relative;
    z-index: 1;
}

#path {
    font-size: var(--font-p-product);
    margin: 0px 0px 10px 0px;
    padding: 8px 0px 4px 0px;
}

.teal-link {
    color: var(--color-theme);
}

/* .teal-link:hover {
    color: var(--color-theme-dark);
    cursor: pointer;
} */

.project-head {
    /* border: 3px solid salmon; */
    display: flex;
}

.project-page h1, h3, ul {
    margin: 0px;
}

/*        ---            image side             ---*/

.image-side {
    flex: 5 1;
}

.selected-img-wrap {
    margin: 0;
    padding: 0;
    width: 100%;
    height: width;
    padding-top: 100%; /* 1:1 Aspect Ratio */
    position: relative; /* If you want image inside of it */
    overflow: hidden;
    border-radius: 8px;
}

.selected-img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; /* to keep the height the same as the width*/
    vertical-align: bottom;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    object-position: top;
}

.imgs-list {
    scrollbar-width: none; /* Firefox */
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
}

.other-img-wrap {
    margin: 12px 10px 0px 0px;
    padding: 0;
    overflow: hidden;
    border-radius: 5px;
    width: 100px;
    height: 100px;
}

.other-img-wrap-selected {
    box-shadow: 0 0 0 3.5px var(--color-theme);
    outline: 2px solid white;
}

.other-img {
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    vertical-align: bottom;
    object-fit: cover;
    object-position: top;
    transition: all .2s linear;
}

.other-img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


/*        ---            sell side             ---*/

.sell-side {
    flex: 6 1;
    /* border: 1px solid coral; */
    padding-left: 20px;
}

.sell-side h1 {
    padding: 0 0 10px 0;
    font-size: var(--font-h1-product);
    line-height: 1.1;
}

.buy-button {
    background: var(--color-theme);
    color: white;
    border: none;
    border-radius: 5px;
    height: 40px;
    width: 100%;
    margin: 20px 0px;
    font-size: var(--font-p-product);
    font-weight: 600;
}

.buy-button:hover {
    background: var(--color-theme-dark);
}

.buy-button:active {
    box-shadow: 0 0 0 2px var(--color-spark-gray);
    outline: 1px solid white;
}

.git-icon {
    width: 20px;
    height: 20px;
}

.info-div {
    border-top: 2px solid var(--color-spark-gray-dark);
    line-height: 1.5;
}

.info-div ul {
    list-style-type: "- ";
    padding-left: 10px;
}

.info-div p, li {
    color: var(--color-spark-gray-dark);
}

/*        ---            description            ---*/
.project-description {
    line-height: 1.5;
}

.project-description p {
    margin-top: 5px;
    margin-bottom: 10px;
    color: var(--color-spark-gray-dark);
    line-height: 1.8;
}

/*    ----------         suggestions           --------- */

.project-suggestions {
    margin: 50px 0px;
}
.footer-outer {
    position: relative;
}

.footer-bg {
    background: url(/portfolion/static/media/bottomatom.cfb2eb07.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center bottom;
    height: 400px;
    width: 100%;
    position: absolute;
    bottom: 0px;
    opacity: .4;
}

.footer {
    color: var(--color-silver-main);
    display: flex;
    justify-content: space-between;
    padding: 0;
    font-family: var(--font-family-main);
    letter-spacing: 2px;
    position: relative;
    z-index: 3;
}



.footer h2 {
    padding: 10px 0px;
    margin: 0px;
}

.footer a {
    color: var(--color-silver-main);
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}

.footer a:visited {
    color: var(--color-silver-main);
}

.icon-line {
    /* margin-bottom: 2rem; */
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    cursor: pointer;
    align-items: center;
}

.icon-line svg {
    font-size: 1.5rem;
    color: var(--color-theme);
    /* margin-top: 3%; */
}

.name-side {
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

.to-top-div {
    margin-bottom: 10px;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: center;
    height: auto;
    -webkit-transform: all .2s ease-in;
            transform: all .2s ease-in;
}

.to-top-div h3 {
    margin: 0;
    padding: 0;
}

.to-top-div svg {
    font-size: 1.4rem;
    
}

.to-top-div:hover {
    cursor: pointer;
    color: var(--color-theme);
}
.squares-wrap {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 20px;
}

/* .square-wrap {
    
} */

.square {
    position: relative;
    text-align: center;
    /* width: 100%;
    height: 100%; */
    width: 200px;
    height: 200px;
}

.square-img-wrap {
    margin: 0px;
    padding: 0px;
    overflow: hidden;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: var(--color-theme);
    box-shadow: 1px 1px 14px var(--color-theme-shadow);
}

.square-img {
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    vertical-align: bottom;
    object-fit: cover;
    object-position: top;
    opacity: 1;
    transition: all .2s linear;
}

.square-img:hover {
    opacity: .3;
}

.image-name {
    margin: 0;
    padding: 0;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); /*make sure words are actually in the center*/
    pointer-events: none; /*text won't interfre with clicks*/
    z-index: 4;
    transition: all .15s linear;
    color: white;
    text-shadow: 0px 0px 15px #2c5a51b6;
}

.square:hover .image-name {
    opacity: 1;
}
.about-page {
    font-family: var(--font-family-google);
    margin-bottom: 50px;
    background: var(--color-bg);
}

.about-page p {
    font-size: var(--font-p-google);
}

.about-page h3 {
    font-size: var(--font-h3-google);
}

.about-page h4 {
    font-size: var(--font-h4-google);
}

#search-p {
    padding: 0px;
    margin: 0px;
    color: var(--color-google-gray);
}

.search-box {
    background-color: #303134;
    border: 1px solid transparent;
    box-shadow: 0 1px 3px rgb(23 23 23 / 24%);
    color: var(--color-silver-main);
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 24px;
    width: 50%;
    height: 50px;
    margin: 20px 0px;
    padding: 0px 23px;
}

.search-box p {
    font-size:calc(var(--font-p-google) + 4px);
}

#search-icon {
    color: var(--color-theme);
    z-index: 9;
    font-size:calc(var(--font-p-google) + 4px);
}

.split-screen {
    margin-top: 20px;
    display: flex;
    /* border: 3px solid blue; */
}

.split-screen h4, .split-screen a {
    color: var(--color-theme);
    text-decoration: none;
    padding: 0px;
    margin: 0px;
}

.split-screen p {
    color: var(--color-google-gray);
    padding: 0px;
    margin: 0px;
}

/* ------------  left side  ---------------- */

.links-side {
    flex: 5 1;
}

.links-side a:hover {
    text-decoration: underline;
}

.links-side a:visited {
    color: #B27EE0;
}

/* ------------  right side  ---------------- */

.info-side {
    flex: 3 1;
    height: auto;
    border: 0;
    border-left: 1px solid #3c4043;
    border-radius: 0;
    padding-left: 20px;
    clear: both;
}

.info-side h3 {
    color: var(--color-google-gray);
    padding: 0px;
    margin: 0px;
}

#blurb {
    line-height: normal;
}


/* ------------  google photos  ---------------- */

.detail-container {
    padding: 10px 0px;
    border-radius: 8px;
    height: auto;
    width: auto;
}

.detail-page {
    height: 200px;
    width: 100%;
    border-radius: 10px;
    overflow: hidden;
    display: grid;
    grid-template-areas: 
        'tall s1 s2'
        'tall long long';
    grid-gap: .3rem;
    gap: .3rem;
    grid-template-columns: 1.5fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}

.box {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.box > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
}

.long-image {
    grid-area: long;
}

.tall-image {
    grid-area: tall;
}

.square-img1 {
    grid-area: s1;
}

.square-img2 {
    grid-area: s2;
}
.landing-page {
    background: var(--color-bg);
}

.inner-landing {
    background: url(/portfolion/static/media/bg1.44b3c210.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center -3vh;
    margin-bottom: 100px;
}

.projects-page {
    font-family: 'Cantarell', sans-serif;
    color: var(--color-silver-main);
    display: flex;
    flex-direction: column;
    background: var(--color-bg);
}

.projects-page .title {
    margin: 0;
    padding: 10px 0px;
}

.squares-body {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.squares-body .squares-wrap {
    flex: 2 1;
}

.space {
    flex: 1 1;
}

.squares-body .square {
    transition: all .3s ease-in;
}

.squares-body .square:hover {
    -webkit-transform: scale(2);
            transform: scale(2);
    z-index: 78;
}
